export default {
	data: function() {
		return {
			placeholderSrc: '../../static/default.jpg',
			show: false,
			imageList:[],
		}
	},
	props:{
		list:{
			type:Array,
			default:function(){
				return [];
			}
		},
		windowHeight:{
			type:Number,
			default:0,
		},
		scrollTop:{
			type:Number,
			default:0,
		}
	},
	methods: {
		load(){
			let that = this
			uni.createSelectorQuery().in(this).selectAll('.image').boundingClientRect((images) => {
				console.log(images)
				images.forEach((image, index) => {
					console.log(index)
					if (image.top <= that.windowHeight) {
						that.imageList[index].show = true;
						that.imageList[index].loaded = true
					}
				})
			}).exec()
		},
		imageLoad(e) {
			this.list[e.target.dataset.index].loaded = true
		}
	},
	watch:{
		windowHeight(newvalue){
			this.windowHeight = newvalue;
		},
		scrollTop(){
			console.log(11)
			this.load();
		},
		imageList(newList){
			this.imageList = newList
		}
	},
	mounted() {
		this.imageList = this.list;
		let _this = this;
		setTimeout(function(){
			_this.load();
		},5000)
		
	}
}
